body{
  margin: 0;
}
.App-content {
  padding: 1rem;
  min-height: 100vh;
}
.container-fluid {
  margin: 0 auto;
  max-width: 1200px;
  padding: 0 15px;
}
.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 15px 0;
}
.link{
  margin-left: 2.25rem;
  padding: 0px .625rem;
  font-size: 18px;
}
.mobileVisible {
  display: none;
}

.mobileHidden {
  display: block;
}

@media only screen and (max-width: 767px) {

  .mobileVisible {
    display: block;
  }
  
  .mobileHidden {
    display: none;
  }
}